@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,300;1,400&display=swap);
*{margin:0;padding:0}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#00075f;color:#eaeaea;font-size:18px;font-family:"Source Sans Pro", sans-serif;font-family:"Fira Code", code;scroll-behavior:smooth}body a:visited,body a{color:#eaeaea;text-decoration:none}#root{min-height:100vh;position:relative}.section{padding:3rem 7rem;min-height:50vh}

.section#landing{padding-top:10rem;min-height:calc(100vh - 18rem);position:relative}.section#landing h1,.section#landing h2{font-family:"Source Sans Pro", sans-serif}.section#landing h1{font-size:3rem;color:#00ffc2;margin:1rem 0}.section#landing h2{font-weight:600;margin-bottom:2rem}.section#landing #contact{display:inline-block;padding:0.4rem 1.2rem;margin-top:6rem;border:2px #00ffc2 solid;border-radius:0.3rem;transition:all 0.2s ease-in-out}.section#landing #contact:hover{color:#00ffa3;background-color:rgba(0,255,162,0.233)}.section#landing #chevron{position:absolute;bottom:2rem;right:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;font-size:2.5rem;color:#00ffc2}.section#landing #chevron svg{-webkit-animation:chevron 3s infinite ease-in alternate;animation:chevron 3s infinite ease-in alternate;margin-top:0;border-radius:1rem}.section#landing #chevron svg:last-child{font-size:3rem;-webkit-animation-delay:-0.5s;animation-delay:-0.5s;margin-top:-1.8rem}@-webkit-keyframes chevron{0%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}50%{-webkit-transform:translate3d(0, 0.3rem, 0);transform:translate3d(0, 0.3rem, 0)}100%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}@keyframes chevron{0%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}50%{-webkit-transform:translate3d(0, 0.3rem, 0);transform:translate3d(0, 0.3rem, 0)}100%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}

.nav{position:sticky;position:-webkit-sticky;padding:0.6rem;top:0;font-size:1.2rem;color:#00ffc2;z-index:99;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center;background:rgba(255,255,255,0.2);backdrop-filter:blur(12px);-webkit-backdrop-filter:blur(15px);border:1px solid rgba(255,255,255,0.18)}.nav span,.nav a:visited,.nav a{padding:0.3rem 0.6rem;color:#eaeaea}.nav a:visited,.nav a{text-decoration:none;transition:all 0.2s ease-in-out}.nav a:visited:hover,.nav a:hover{color:#00ffc2}.nav a:first-child{padding:0.4rem 1.2rem;border:2px #00ffc2 solid;margin-right:1rem;border-radius:0.3rem}.nav a:first-child:hover{color:#00ffa3;background-color:rgba(0,255,162,0.233)}

#About{max-width:50ch}

#sidelinks{position:fixed;right:0;top:calc(50vh + 2rem);margin-right:1.5rem;-webkit-transform:translateY(-50%);transform:translateY(-50%)}#sidelinks .container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}#sidelinks .container a:visited,#sidelinks .container a{transition:all 0.2s ease-in-out;color:#eaeaea;text-decoration:none;padding:0.5rem 0;font-size:1.5rem;position:relative}#sidelinks .container a:visited span,#sidelinks .container a span{transition:all 0.2s ease-in-out;width:8ch;text-align:center;border-radius:0.25rem;color:#00ffc2;font-size:1rem;padding:0.5rem;display:none;position:absolute;right:3rem;top:50%;-webkit-transform:translateY(-55%);transform:translateY(-55%);background:#030e38;box-shadow:1px 1px 2px #00ffc2}#sidelinks .container a:visited:hover,#sidelinks .container a:hover{color:#00ffc2}#sidelinks .container a:visited:hover span,#sidelinks .container a:hover span{opacity:1;display:inline}#sidelinks .container::before,#sidelinks .container::after{content:"";width:0;border:solid #eaeaea 1.5px;background-color:#eaeaea;position:fixed;height:calc((80vh / 2) - 4rem);border-radius:2rem;right:50%}#sidelinks .container::before{top:0;-webkit-transform:translate(50%, -105%);transform:translate(50%, -105%)}#sidelinks .container::after{-webkit-transform:translate(50%, 105%);transform:translate(50%, 105%);bottom:0}

